// src/Login.js
import React, { useState, useEffect  } from 'react';
import { Form, Input, Button, Checkbox, message,  Typography } from 'antd';
import 'antd/dist/reset.css'

const { Title, Text, Link } = Typography;

const Login = () => {


    const [isForgotPassword, setIsForgotPassword] = useState(false); // Estado para alternar entre formularios
    const [isLoading, setIsLoading] = useState(false); // Estado para bloquear el botn mientras se ejecuta la peticin

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [remember, setRemember] = useState(false); // Estado para controlar el checkbox de recordar credenciales


    const currentYear = new Date().getFullYear();
    const [login] = Form.useForm();

    const [loading, setLoading] = useState(false);

    useEffect(() => {
        // Cargar credenciales desde localStorage si existen
        const savedEmail = localStorage.getItem('email');
        const savedPassword = localStorage.getItem('password');
        const savedRemember = localStorage.getItem('remember') === 'true';
    
        setRemember(true);

        if (savedEmail && savedPassword) {
            setEmail(savedEmail);
            setPassword(savedPassword);
            setRemember(true);
          }
      }, []);

  const onRememberChange = (e) => {
        setRemember(e.target.checked);
      };

    const onFinish = async (e) => {

        e.preventDefault();

        setLoading(true);
        setIsLoading(true);

        const values = {

            Usuario :email,
            Contrasena: password
        }

        // Simula una peticin al backend
        const response = await fetch('/Login/Login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(values),
        });

        setLoading(false);

        if (response.ok) {
    
            const data = await response.json();
            if (data.Exito) {
 
                if (remember) {
                    // Guardar credenciales si "Recordar credenciales" est marcado
                    localStorage.setItem('email', email);
                    localStorage.setItem('password', password);
                    localStorage.setItem('remember', true);
                  } else {
                    // Eliminar credenciales si no est marcado
                    localStorage.removeItem('email');
                    localStorage.removeItem('password');
                    localStorage.setItem('remember', false);
                  }

                window.location.href = data.URL;
            }
            else {
                setIsLoading(false);
                message.error(data.Mensaje);
            }

   

          
        } else {
            setIsLoading(false);
            const error = await response.json();
            message.error('Error al ejecutar la peticin');
         /*   message.error(error.message || 'Error en el inicio de sesin');*/
        }
    };


    const onFinishForgotPassword = async (values) => {

        setIsLoading(true);
        setLoading(true);

        // Simula una peticin al backend
        const response = await fetch('/Login/RecuperarContrasena', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(values),
        });

        setLoading(false);

        if (response.ok) {
    
            const data = await response.json();
            if (data.Exito) {
 
                message.success('Se ha enviado un correo para recuperar tu contrasea a ' + values.Usuario );
                setIsForgotPassword(false); // Regresa al formulario de inicio de sesin
                setIsLoading(false);
            }
            else {
                setIsLoading(false);
                message.error(data.Mensaje);
            }

   

          
        } else {
            setIsLoading(false);
            message.error('Error al ejecutar la peticin');


        }

       
      };



    return (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', height: '100vh', backgroundColor: '#f0f2f5' }}>
            <img src="../Content/Project/Imagenes/LogoPrincipal.png" alt="yitpro logo" style={{ width: 189, height:69, marginBottom: 20 }} /> {/* Reemplaza 'logo.png' con la ruta correcta de tu logo */}
            <div style={{ width: 300, padding: 20, backgroundColor: 'white', borderRadius: 8, boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)' }}>
                <Title level={4} style={{ textAlign: 'center', marginBottom: 20 }}>  {isForgotPassword ? 'Recuperar contrasea' : 'Inicia sesin con tu cuenta'}</Title>


                {!isForgotPassword ? (
                <Form
                    form={login}
                    name="login"
                    initialValues={{ remember: true }}
                    onSubmitCapture ={onFinish}
               
                    layout="vertical"
                >
                    <Form.Item
                       /* name="Usuario"*/
                        
                        rules={[
                            { required: true, message: 'Por favor, ingresa tu correo electrnico' }, { 
                            type: 'email', 
                            message: 'Por favor, ingresa un correo electrnico vlido' 
                          }]}
                    >
                        <Input  
                          value={email}
                            onChange={(e) => setEmail(e.target.value)} 
                            placeholder="Correo electrnico" />
                    </Form.Item>

                    <Form.Item
                     /*   name="Contrasena"*/
                        rules={[{ required: true, message: 'Por favor, ingresa tu contrasea' }]}
                    >
                        <Input.Password 
                            value={password}
                                        onChange={(e) => setPassword(e.target.value)}
                                     placeholder="Contrasea" />
                    </Form.Item>

                    <Form.Item /*name="remember" */valuePropName="checked" style={{ marginBottom: 10 }}>
                        <Checkbox  checked={remember} onChange={onRememberChange} >Recordar credenciales</Checkbox>
                    </Form.Item>

                    <Form.Item>
                        <Button type="primary" htmlType="submit" block   loading={isLoading} style={{ backgroundColor: '#0057FF', borderColor: '#0057FF' }}>
                            Ingresar
                        </Button>
                    </Form.Item>
                </Form>

              
            ) : (

                <Form
                name="forgotPassword"
                layout="vertical"
                onFinish={onFinishForgotPassword}
              >
                <Form.Item
                  name="Usuario"
                  rules={[
                    { required: true, message: 'Por favor, ingresa tu correo electrnico' },
                    { type: 'email', message: 'Por favor, ingresa un correo electrnico vlido' },
                  ]}
                >
                  <Input placeholder="Ingresa tu correo electrnico" />
                </Form.Item>
    
                <Form.Item>
                  <Button type="primary" htmlType="submit" block   loading={isLoading}> 
                    Enviar correo de recuperacin
                  </Button>
                </Form.Item>
              </Form>

            )}
      {!isForgotPassword ? (
          <Link href="#" onClick={() => setIsForgotPassword(true)} style={{ display: 'block', textAlign: 'center', marginTop: 10 }}>
            Olvid su contrasea?
          </Link>
        ) : (
          <Link href="#" onClick={() => setIsForgotPassword(false)} style={{ display: 'block', textAlign: 'center', marginTop: 10 }}>
            Volver al inicio de sesin
          </Link>)}
            </div>
            <Text style={{ marginTop: 20, color: '#aaa' }}>{currentYear} - Axsis tecnologa</Text>
        </div>
    );
};



export default Login;
