import React, { useState, useEffect } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { Card, Typography, Tag, Spin } from "antd";
import axios from "axios";

const { Title, Text } = Typography;

const Kanban = () => {
    const [columns, setColumns] = useState({});
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchTasks = async () => {
            try {
                const response = await axios.get("/Actividades/GetTasks"); // Endpoint que devuelve los datos
                const tasks = response.data;

                const organizedColumns = tasks.reduce((acc, task) => {
                    const columnKey = task.columna;
                    if (!acc[columnKey]) {
                        acc[columnKey] = { title: columnKey, tasks: [] };
                    }
                    acc[columnKey].tasks.push({
                        id: task.id,
                        titulo: task.titulo,
                        descripcion: task.descripcion,
                        fecha: task.fecha,
                        tiempo: task.tiempo,
                    });
                    return acc;
                }, {});

                setColumns(organizedColumns);
            } catch (error) {
                console.error("Error al obtener las tareas:", error);
            } finally {
                setLoading(false);
            }
        };

        fetchTasks();
    }, []);

    const onDragEnd = (result) => {
        const { source, destination } = result;
        if (!destination) return;

        const sourceColumn = columns[source.droppableId];
        const destColumn = columns[destination.droppableId];
        const sourceTasks = Array.from(sourceColumn.tasks);
        const destTasks = Array.from(destColumn.tasks);

        const [movedTask] = sourceTasks.splice(source.index, 1);
        destTasks.splice(destination.index, 0, movedTask);

        const updatedColumns = {
            ...columns,
            [source.droppableId]: { ...sourceColumn, tasks: sourceTasks },
            [destination.droppableId]: { ...destColumn, tasks: destTasks },
        };

        setColumns(updatedColumns);
    };

    if (loading) {
        return <Spin size="large" />;
    }

    return (
        <DragDropContext onDragEnd={onDragEnd}>
            <div className="kanban-board">
                {Object.entries(columns).map(([columnId, column]) => (
                    <Droppable key={columnId} droppableId={columnId}>
                        {(provided) => (
                            <div
                                className="kanban-column"
                                {...provided.droppableProps}
                                ref={provided.innerRef}
                            >
                                <Title level={4}>
                                    {column.title} ({column.tasks.length})
                                </Title>
                                {column.tasks.map((task, index) => (
                                    <Draggable key={task.id} draggableId={task.id} index={index}>
                                        {(provided) => (
                                            <Card
                                                {...provided.draggableProps}
                                                {...provided.dragHandleProps}
                                                ref={provided.innerRef}
                                            >
                                                <Title level={5}>{task.titulo}</Title>
                                                <Text>{task.descripcion}</Text>
                                                <Tag>{task.fecha}</Tag>
                                            </Card>
                                        )}
                                    </Draggable>
                                ))}
                                {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                ))}
            </div>
        </DragDropContext>
    );
};

export default Kanban;
